<template>

  <div class="modal-body" style="height:auto">
    <!-- Nav tabs -->
    <ul class="nav nav-tabs" role="tablist">
      <li role="presentation" class="active"><a href="#home" aria-controls="home" role="tab" data-toggle="tab"></a></li>
      <li role="presentation"><a href="#profile" aria-controls="profile" role="tab" data-toggle="tab"></a></li>
    </ul>

    <!-- Tab panes -->
    <div class="tab-content">

      <div role="tabpanel" class="tab-pane active" id="home" >

        <div class="line line-dashed line-lg pull-in"></div>
        <div class="form-group">
          <div class="form-group" style="width:100%;">
            <input id="JSh_ZhP" type="file" class="file" name="file" multiple>
          </div>
        </div>
      </div>

      <div role="tabpanel" class="tab-pane" id="profile">

        <div class="btn-group" data-toggle="buttons" style="margin-top:2%;float:left">
          <label @click="typeChange(0)" class="btn btn-default">
            <input type="radio" name="options" id="option1" autocomplete="off" checked>
          </label>
          <label @click="typeChange(1)" class="btn btn-default">
            <input type="radio" name="options" id="option2" autocomplete="off">
          </label>
          <label @click="typeChange(2)" class="btn btn-default">
            <input type="radio" name="options" id="option3" autocomplete="off">
          </label>
          <label @click="typeChange(3)" class="btn btn-default">
            <input type="radio" name="options" id="option4" autocomplete="off" checked>
          </label>
          <label @click="typeChange(4)" class="btn btn-default">
            <input type="radio" name="options" id="option5" autocomplete="off" checked>
          </label>
        </div>

        <table class="table table-striped" id="tab">
        </table>

      </div>

    </div>

  </div>

</template>

<script>

  import ModalBox from '../ModalBox/AddStudent'
  import 'bootstrap-table/js/bootstrap-table.min.js'
  import 'bootstrap-table/js/bootstrap-table-zh-CN.min.js'
  import 'bootstrap-table/css/bootstrap-table.min.css'
  import '../../../static/bootstrap-datetimepicker-master/js/bootstrap-datetimepicker.js'
  import 'bootstrap-fileinput/js/fileinput.min.js'
  import 'bootstrap-fileinput/css/fileinput.min.css'
  import 'bootstrap-fileinput/js/zh.js'
  import  'jquery'


  export default {
    name: 'addContent',

    data () {
      return {

        id:"",//id
        type:'0',//

      }
    },
    watch:{
      //nodeId
      nodeId(val){
        $("#tab").bootstrapTable('refresh');
        $('#JSh_ZhP').fileinput('clear');
        $('#JSh_ZhP').fileinput('destroy');
        $("#JSh_ZhP").fileinput({
          showUpload: true,//
          showRemove: true,//
          uploadAsync: true,//
          showCaption: true,//
          language: "zh",//
          uploadUrl: "/api/addContent/upload",
          ajaxSettings:{headers :{token:window.sessionStorage.getItem("token")}},
          maxFileSize: 0,//kb0
          maxFileCount: 20, /**/
          msgFilesTooMany: "({n}) {m}",
          dropZoneTitle: "",
          dropZoneClickTitle: "",
          showBrowse: false,
          showPreview: true,
          browseOnZoneClick: true,
          dropZoneEnabled: true,//
          layoutTemplates: {
            actionZoom: ''
          },uploadExtraData:{
            fileName:"fileName",
            nodeId:val
          },
        });
      }
    },

    props: {
      nodeId: {
        type: Number,
        default: 0
      }
    },

    methods:{

      //table
      typeChange(val){
        this.type = val;
        $("#tab").bootstrapTable('refresh');
      },

      //
      deleteAfter(){
        axios({
          method: 'post',
          url: '/api/deleteFile',
          data:{
            id:this.id,
          }
        }).then((response) => {
          $("#tab").bootstrapTable('refresh');
        }).catch(function(error) {
          bus.$emit("dialog",{
            type:"false",
            body:"",
            auto:false,
          })
        });
      },

    },
    beforeCreate(){},
    created(){},
    beforeMount(){},
    mounted() {
      var th = this;

      $("#tab") .bootstrapTable({
        url:'/api/getFileInfoById',
        ajaxOptions:{
          headers: {"token":window.sessionStorage.getItem("token")}
        },
        cache:false,
        toolbar:'#toolbar',
        striped:true,//
        showRefresh:true,
        method:'post',
        sortOrder:'desc',
        sortName:'id',
        sidePagination:'server',
        pagination:true,
        paginationLoop:false,
        pageNumber:'1',
        pageSize:'10',
        pageList:'[10, 25, 50, 100, All]',
        clickToSelect: true,
        showToggle:true,
        columns:[
          {field: "fileName", title: ""},
          {field: "fileType", title: ""},
          {field: "gmtCreate", title: "",formatter: format},
          {field:"operation", title:"",align: 'center', valign: 'middle',width:'50',formatter: operateFormatter}
        ],queryParams:function(params) {
          params.tags = th.type;
          params.nodeId=th.nodeId;
          return params;
        },responseHandler:function(res){
          return res.result;
      }
      });

      $("#JSh_ZhP").fileinput({

        showUpload: true,//
        showRemove: true,//
        uploadAsync: true,//
        showCaption: true,//
        language: "zh",//
        uploadUrl: "/api/addContent/upload",
        ajaxSettings:{headers :{token:window.sessionStorage.getItem("token")}},
        maxFileSize: 0,//kb0
        maxFileCount: 20, /**/
        msgFilesTooMany: "({n}) {m}",
        dropZoneTitle: "",
        dropZoneClickTitle: "",
        showBrowse: false,
        showPreview: true,
        browseOnZoneClick: true,
        dropZoneEnabled: true,//
        layoutTemplates: {
          actionZoom: ''
        },uploadExtraData:{
          fileName:"fileName",
          nodeId:th.nodeId
        },
      });

      //
      $('#JSh_ZhP').on("fileuploaded", function (event, data) {
        var result = data.response; //json
        if(data.response.status==1)
        {
          $("#tab").bootstrapTable('refresh');
          bus.$emit("dialog",{
            type:"success",
            body:"",
            auto:true,
            time:3000   //
          });
        }else{
          bus.$emit("dialog",{
            type:"false",
            body:"",
            auto:false,
          })
        }
      });

      //table
      function operateFormatter(value, row, index) {//
        return [
          '<button class="RoleOfdelete btn btn-danger" style="margin:0 10px 0 0" OnClick="deleteModel('+row.id+')"></button>'
        ]
      }
      window.deleteModel = function (value) {
        th.id = value;
        var r=confirm("");
        if (r==true)
        {
          th.deleteAfter();
          bus.$emit("dialog",{
            type:"false",
            body:"",
            auto:true,
            time:3000
          })
        }
      };

      //yyyy-MM-dd hh-mm-ss
      function add0(m){return m<10?'0'+m:m }
      function format(timeStamp)
      {
        //timeStampparseInt
        var time = new Date(timeStamp);
        var y = time.getFullYear();
        var m = time.getMonth()+1;
        var d = time.getDate();
        var h = time.getHours();
        var mm = time.getMinutes();
        var s = time.getSeconds();
        return y+'-'+add0(m)+'-'+add0(d)+' '+add0(h)+':'+add0(mm)+':'+add0(s);
      }
    },
    beforeUpdate(){},
    updated(){},
    beforeDestroy(){},
    destroyed(){}
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0;
  }

  a {
    color: #42b983;
  }

</style>



